import React from 'react';
import URLSearchParams from 'url-search-params';
import { Route } from 'react-router-dom';
import { Panel } from 'react-bootstrap';

import graphQLFetch from './graphQLFetch.js';
import IssueTable from './IssueTable.jsx';
import IssueFilter from './IssueFilter.jsx';
import IssueDetail from './IssueDetail.jsx';
import Toast from './Toast.jsx';
import store from './store.js';

export default class IssueList extends React.Component {
	constructor() {
		super();
		// try to retrieve any available data first
		const issues = store.initialData ? store.initialData.issueList : null;
		delete store.initialData;
		this.state = {
			issues,
			toastVisibility: false,
			toastMessage: '',
			toastType: 'info'
		};
		this.closeIssue = this.closeIssue.bind(this);
		this.deleteIssue = this.deleteIssue.bind(this);
		this.showSuccess = this.showSuccess.bind(this);
		this.showError = this.showError.bind(this);
		this.dismissToast = this.dismissToast.bind(this);
	}

	componentDidMount() {
		// load the data if we don't have it yet
		const { issues } = this.state;
		if (issues == null) this.loadData();
	}

	componentDidUpdate(prevProps) {
		// making sure to fetch the data again if there is a change in the query params
		const {
			location: { search: prevSearch }
		} = prevProps;
		const {
			location: { search }
		} = this.props;
		if (prevSearch !== search) {
			this.loadData();
		}
	}

	static async fetchData(match, search, showError) {
		// parse and check if there are any query parameters
		const params = new URLSearchParams(search);
		const vars = {};
		if (params.get('status')) vars.status = params.get('status');

		const effortMin = parseInt(params.get('effortMin'), 10);
		if (!Number.isNaN(effortMin)) vars.effortMin = effortMin;

		const effortMax = parseInt(params.get('effortMax'), 10);
		if (!Number.isNaN(effortMax)) vars.effortMax = effortMax;

		// use the named operation for the query, in case a query parameter was passed
		const query = `query issueList(
			$status: StatusType
			$effortMin: Int
			$effortMax: Int
			) {
			issueList (
				status: $status
				effortMin: $effortMin
				effortMax: $effortMax
				) {
				id title status owner
				created effort due
			}
		}`;

		const data = await graphQLFetch(query, vars, this.showError);
		return data;
	}

	showSuccess(message) {
		this.setState({
			toastVisibility: true,
			toastMessage: message,
			toastType: 'success'
		});
	}

	showError(message) {
		this.setState({
			toastVisibility: true,
			toastMessage: message,
			toastType: 'danger'
		});
	}

	dismissToast() {
		this.setState({
			toastVisibility: false
		});
	}

	async loadData() {
		const {
			location: { search }
		} = this.props;

		const data = await IssueList.fetchData(null, search, this.showError);
		if (data) {
			this.setState({ issues: data.issueList });
		}
	}

	// async createIssue(issue) {
	// 	const query = `mutation issueAdd($issue: IssueInputs!) {
	//   		issueAdd(issue: $issue) {
	//     		id
	//   		}
	// 	}`;

	// 	// if adding issue was successful, then refresh data
	// 	const data = await graphQLFetch(query, { issue }, this.showError);
	// 	if (data) {
	// 		this.loadData();
	// 	}
	// }

	// gets triggered by an IssueRow button
	async closeIssue(index) {
		const query = `mutation issueClose($id: Int!) {
            issueUpdate(id: $id, changes: { status: Closed }) {
                id title status owner effort created due description
            }
        }`;
		const { issues } = this.state;
		const data = await graphQLFetch(
			query,
			{ id: issues[index].id },
			this.showError
		);
		if (data) {
			// creating a new list and replacing the old item with our new one
			this.setState((prevState) => {
				const newList = [...prevState.issues];
				newList[index] = data.issueUpdate;
				return { issues: newList };
			});
		} else {
			this.loadData();
		}
	}

	async deleteIssue(index) {
		const query = `mutation issueDelete($id: Int!) {
			issueDelete(id: $id)
		  }`;
		const { issues } = this.state;

		const {
			location: { pathname, search },
			history
		} = this.props;
		const { id } = issues[index];

		const data = await graphQLFetch(query, { id }, this.showError);
		if (data && data.issueDelete) {
			this.setState((prevState) => {
				const newList = [...prevState.issues];
				// if the user is deleting the selected item, we redirect them back to an unselected view
				if (pathname === `/issues/${id}`) {
					history.push({ pathname: '/issues', search });
				}
				newList.splice(index, 1);
				return { issues: newList };
			});
			this.showSuccess(`Deleted Issue ${id} successfully.`);
		} else {
			//if api call fails, we reload
			this.loadData();
		}
	}

	render() {
		// precheck to ensure we have the data
		const { issues } = this.state;
		if (issues == null) return null;

		const hasFilter = location.search !== '';

		const { toastVisibility, toastMessage, toastType } = this.state;
		return (
			<React.Fragment>
				<Panel defaultExpanded={hasFilter}>
					<Panel.Heading>
						<Panel.Title toggle>Filter</Panel.Title>
					</Panel.Heading>
					<Panel.Body collapsible>
						<IssueFilter />
					</Panel.Body>
				</Panel>
				<hr />
				<IssueTable
					issues={this.state.issues}
					closeIssue={this.closeIssue}
					deleteIssue={this.deleteIssue}
				/>
				{/* <IssueAdd createIssue={this.createIssue} /> */}
				<Route path={`${this.props.match.path}/:id`} component={IssueDetail} />
				<Toast
					showing={toastVisibility}
					onDismiss={this.dismissToast}
					bsStyle={toastType}
				>
					{toastMessage}
				</Toast>
			</React.Fragment>
		);
	}
}
